// hero block
.hero {
	@include r(500) {
		margin-top: 30px; } }

.hero__txt {
	margin-top: 45px;
	text-align: center;
	text-transform: uppercase;
	@include gb;
	font-size: 117px;
	line-height: 1;
	letter-spacing: 0.05em;
	color: #000;
	background: -webkit-linear-gradient(transparent, transparent), url(../img/coming-soon.jpg) repeat;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	-moz-background-clip: text;
	-moz-text-fill-color: transparent;
	white-space: nowrap;
	@include r(1200) {
		white-space: normal;
		font-size: 100px; }
	@include r(1020) {
		font-size: 80px; }
	@include r(767) {
		font-size: 70px; }
	@include r(500) {
		font-size: 55px; } }


.hero__date {
	margin: 38px 0 88px;
	text-transform: uppercase;
	text-align: center;
	@include gl;
	color: #000;
	font-size: 89px;
	line-height: 80px;
	letter-spacing: 0.31em;
	@include r(1020) {
		font-size: 70px; }
	@include r(767) {
		margin: 38px 0 60px 15px;
		font-size: 50px; } }

// description section
.desc {
	padding: 96px 0;
	background: #FFF;
	text-align: center;
	@include r(500) {
		padding: 80px 0 70px; } }

.desc__subtitle {
	margin-top: 5px;
	text-transform: uppercase;
	@include gl;
	font-size: 32px;
	color: #232323;
	font-weight: 300;
	letter-spacing: 0.04em;
	@include r(1020) {
		margin-top: 10px; }
	@include r(767) {
		font-size: 28px; }
	@include r(500) {
		font-size: 20px; } }

.desc__text {
	margin-top: 13px;
	color: #232323;
	@include dt;
	font-size: 18px;
	font-weight: 400;
	line-height: 1.45; }

.desc__btn {
	margin-top: 20px; }


// join section
.join {
	position: relative;
	background-image: url(../img/main-bg.jpg);
	@include cover;
	z-index: 0;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(#000, 0.2);
		z-index: -1;
		@include r(500) {
			background: rgba(#000, 0.5); } } }

.join__in {
	@include clr; }

.join__bl {
	float: right;
	max-width: 405px;
	padding: 50px 0 60px;
	font-size: 18px;
	color: #FFF;
	line-height: 1.2;
	@include g;
	@include r(1020) {
		max-width: 350px; }
	@include r(767) {
		max-width: 300px; }
	@include r(500) {
		max-width: initial; } }

.join__ttl {
	text-transform: uppercase;
	font-size: 35px;
	margin-bottom: 10px;
	@include r(1020) {
		font-size: 30px; }
	@include r(767) {
		font-size: 28px; }
	@include r(500) {
		font-size: 25px; } }

.join__btn {
	margin-top: 27px; }


